import Axios, {
  AxiosRequestConfig,
  AxiosResponse,
  AxiosError,
  AxiosInstance,
} from "axios";
import { Request } from "@/utils/http/types";

import { ElMessage } from "element-plus";
/**
 *  element-plus 
 * @param msg
 * @param type
 */
const message = (msg: string, type?: string) => {
  ElMessage({
    message: msg,
    type: type || "warning",
    duration: 1500,
  });
};

/**
 *  create Axios 
 */
const defaultConfig: AxiosRequestConfig = {
  baseURL: "",
  timeout: 10000, //10
  withCredentials: true,
  responseType: "json",
  transformRequest: [
    (data) => {
      //
      data = JSON.stringify(data);
      return data;
    },
  ],
  validateStatus() {
    // async-awaitrejectresolve
    return true;
  },
  transformResponse: [
    (data) => {
      //
      if (typeof data === "string" && data.startsWith("{")) {
        data = JSON.parse(data);
      }
      return data;
    },
  ],
  headers: {
    Accept: "application/json, text/plain, */*",
    "Content-Type": "application/json",
    "X-Requested-With": "XMLHttpRequest",
  },
};
/**
 * Axios create
 * @param config
 */
const getConfig = (config?: AxiosRequestConfig): AxiosRequestConfig => {
  if (!config) return defaultConfig;
  return defaultConfig;
};

/**
 * Axios 
 */
class EnclosureHttp {
  constructor() {
    this.httpInterceptorsRequest();
    this.httpInterceptorsResponse();
  }

  /**
   * Axios 
   * @private
   */
  private static axiosInstance: AxiosInstance = Axios.create(getConfig());

  /**
   * 
   * @private
   */
  private httpInterceptorsRequest(): void {
    EnclosureHttp.axiosInstance.interceptors.request.use(
      (config: AxiosRequestConfig) => {
        /*
         * 
         * */
        // console.log("config=>:", config);
        return config;
      },
      (err) => {
        return Promise.resolve(err);
      }
    );
  }

  /**
   * 
   * @private
   */
  private httpInterceptorsResponse(): void {
    EnclosureHttp.axiosInstance.interceptors.response.use(
      (response: AxiosResponse) => {
        /*
         *   
         * */
        const { status } = response;
        let msg = "";
        if (status < 200 || status >= 300) {
          // http
          if (typeof response.data === "string") {
            msg = "";
            response.data = { msg };
          } else {
            response.data.msg = msg;
          }
        }
        return response;
      },
      (error: AxiosError) => {
        //
        const { response } = error;
        if (response) {
          // 2xx
          this.errorHandle(response.status, response.data.message);
          return Promise.reject(response);
        } else {
          // 
          // eg:statenetwork
          // networkapp.vue
          // 
          return Promise.reject(error);
        }
      }
    );
  }

  /**
   * 
   * @param status 
   * @param other
   */
  private errorHandle = (status: number, other: string) => {
    // 
    switch (status) {
      case -1: // -1: 
        message("");
        break;
      case 403: // 403 token
        message("");
        break;
      case 404: // 404
        message("");
        break;
      default:
        message(other);
    }
  };

  /**
   * get
   * @param url 
   * @param params 
   * @param config
   */
  public reqGet: Request = async (
    url: string,
    params?: unknown,
    config?: AxiosRequestConfig
  ) => {
    return await EnclosureHttp.axiosInstance.get(url, { params, ...config });
  };

  /**
   * post 
   * @param url 
   * @param params 
   * @param config
   */
  public reqPost: Request = (
    url: string,
    params: unknown = {},
    config?: AxiosRequestConfig
  ) => {
    return EnclosureHttp.axiosInstance.post(url, { data: params }, config);
  };

  /**
   * Axios init GET
   * @param url 
   * @param params 
   * @param config
   */
  public get: Request = (
    url: string,
    params?: unknown,
    config?: AxiosRequestConfig
  ) => {
    return Axios.get(url, { params, ...config });
  };

  /**
   * Axios init POST 
   * @param url 
   * @param params 
   * @param config
   */
  public post: Request = (
    url: string,
    params: unknown = {},
    config?: AxiosRequestConfig
  ) => {
    return Axios.post(url, { data: params }, config);
  };
}

export default EnclosureHttp;
